import { MyButton } from "./MyButton";
import { Drawer, Form } from 'antd';
import { useEffect, useState } from "react";
import Loading from "./Loading";
import { Fn } from "../../helper/function";
import { MyFormItem } from "./Input";

 /**
  * // TODO: Component search
  * Gets script version
  * params:
  * - muityChoose: Cho phép chọn nhiều hay không? Mặc định = false
  * - onSearch: Fn excute search
  * - resultKey: Key kết quả
  * - resultName: Name kết quả
  * - setResultKey: fn set resultKey
  * - setResultName: fn set resultName
  * - dataSearch:
  * - label: 
  * - isLoading: 
  * ex:
  * - muityChoose={false}
  * - onSearch={onSearch}
  * - resultKey={resultKey}
  * - resultName={resultName}
  * - setResultKey={setResultKey}
  * - setResultName={setResultName}
  * - dataSearch={dataSearch}
  * - label={label}
  * - isLoading={isLoading}
  * @returns script version
  */

export default function SearchComponent(props: any) {
    let muityChoose: boolean|undefined = props.muityChoose;
    if (muityChoose === undefined) {
        muityChoose = false;
    }

    const [visible, setVisible] = useState(false);

    function handleSearch() {
        setVisible(true);
    }

    function onCloseDrawer() {
        setVisible(false);
    }

    function handleSearchData(params: any) {
        props.onSearch(params);
    }

    useEffect(() => {
    }, [])

    function handleChooseSearch(data: any) {
        if (muityChoose === true) {
            var a = props.resultName.split(",");
            var b = Fn.GetIndexInArrayStr(a, data.name);
            
            if(b !== -1) {
                console.log("EXIST ARRAY");
                return;
            }
            props.setResultKey(`${props.resultKey}${props.resultKey !== '' ? ', ' : ''}${data.key}`);
            props.setResultName(`${props.resultName}${props.resultName !== '' ? ', ' : ''}${data.name}`);
            // setVisible(false);
        } else {
            props.setResultKey(data.key);
            props.setResultName(data.name);
            // setVisible(false);
        }
    }

    function renderDataSearch() {
        return props.dataSearch.map((data: any, index: number) => {
            return (
                <MyButton key={data.key} value={data.name} onClick={() => handleChooseSearch(data)} />
            );
        })
    }

    function deleteResult() {
        props.setResultKey('');
        props.setResultName('');
        // setVisible(false);
    }

    return (
        <div>
            <MyFormItem.Input
                label={props.label}
                onClick={handleSearch}
                readOnly={true}
                value={props.resultName} />

            <Drawer
                placement="top"
                open={visible}
                onClose={onCloseDrawer}>
                <div>
                    <Loading isLoading={props.isLoading} />
                    <MyButton value="Xóa tìm kiếm" onClick={deleteResult} />
                    <MyFormItem.Input readOnly={true} label="Kết quả" value={props.resultName}/>
                    <Form>
                        <MyFormItem.Search label="Tìm kiếm" onSearch={handleSearchData} />
                    </Form>
                    {renderDataSearch()}
                </div>
            </Drawer>
        </div>
    );
}
